<template>
    <div>
        <h1>系统导航</h1>
        <ul>
            <li v-for="item of systemArr" :key="item.id">
                <span @click="handleClickSystem(item.url)">{{item.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                systemArr: [
                    {id: 'plugins', name: '插件使用系统', url: 'http://local-client.xf.com/home'},
                    {id: 'baidu',name: '百度', url: 'http://www.baidu.com'},
                ]
            }
        },
        methods: {
            handleClickSystem(url) {
                window.href = url
            }
        }
    }
</script>

